.fcard {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  padding: 20px;
  border-radius: 2px;
  height: 100px;
  margin: 1rem;
  position: relative;
  width: 100px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  -webkit-animation-name: flip-in-hor-bottom;
          animation-name: flip-in-hor-bottom;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.unmatched {
  background-color: white;
}

.matched {
  background-color: #2A77AE;
  color: white;
}

.pending {
  color: white;
  -webkit-animation-name: color-change-4x;
          animation-name: color-change-4x;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

